import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators} from '@angular/forms';
import { Router } from '@angular/router';
import { MatSnackBar } from '@angular/material/snack-bar';
import { matchingPasswords, emailValidator } from 'src/app/theme/utils/app-validators';
import { Store, select } from '@ngrx/store';
import { GlobalState } from 'src/app/store/states/global.state';
import { User } from './models/user';
import { UserInsertAction } from './store/user.actions';
import { selectAllUser, selectCurrentUser} from './store/user.selectors';
import { AuthService } from './services/auth.service';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss']
})
export class RegisterComponent implements OnInit {
  public registerForm: FormGroup;
  public hide = true;
  public userTypes = [
    { id: 1, name: 'Agent' },
    { id: 2, name: 'Agency' },
    { id: 3, name: 'Buyer' },
    { id: 7, name: 'Seller' },
    { id: 4, name: 'LandLord' },
    { id: 5, name: 'CareTaker' },
    { id: 6, name: 'Tenant' },
  ];
  public genderTypes = [
    {id: 1, name: 'MALE'},
    {id: 2, name: 'FEMALE'}
  ];
  constructor(public fb: FormBuilder, public router: Router, public snackBar: MatSnackBar, private store: Store<GlobalState> , private auth: AuthService) { }

  ngOnInit() {
    this.registerForm = this.fb.group({
      userType: ['', Validators.required],
      username: ['', Validators.compose([Validators.required, Validators.minLength(6)])],
      fullName: ['', Validators.compose([Validators.required, Validators.minLength(6)])],
      gender: ['', Validators.compose([Validators.required, Validators.minLength(3)])],
      dob: ['', ],
      phone: ['', Validators.compose([Validators.required, Validators.minLength(9)])],
      email: ['', Validators.compose([Validators.required, emailValidator])],
      password: ['', Validators.required],
      confirmPassword: ['', Validators.required],
      receiveNewsletter: false
    }, {validator: matchingPasswords('password', 'confirmPassword')});

    this.store.pipe(select(selectCurrentUser)).subscribe(user => this.checkUsers(user) );
  }

  private checkUsers(user: User){
    if (user){
        this.auth.saveUser(user, null);
        this.router.navigate(['/account/my-properties']);
    }
  }

  public onRegisterFormSubmit(values: Object): void {
    if (true) {
      const user: User = this.registerForm.value as User;
      this.store.dispatch(new UserInsertAction(user));
      this.snackBar.open('Registering user', 'Ã—', { panelClass: 'success', verticalPosition: 'top', duration: 3000 });
    }
  }
}
